import DeepData from "../../assets/js/formate.js";
export default {
  data() {
    return {
      avator_nameO: "",
      end_time: "",
      start_time: "",
      optionchannels: [],
      tableData: [],
      currentPage: 1,
      limit: 10,
      total: "",
      titleData:[
        {
          prop:"day",
          label:"日期"
        },
        {
          prop:"reg_user",
          label:"新增账号"
        },
        {
          prop:"active_user",
          label:"活跃账户"
        },
        {
          prop:"reg_user_game",
          label:"新增玩牌人数"
        },
        {
          prop:"active_user_game",
          label:"活跃玩牌人数"
        },
        {
          prop:"reg_ratio",
          label:"注册玩牌率"
        },
        {
          prop:"active_ratio",
          label:"活跃玩牌率"
        },
        {
          prop:"game_user",
          label:"玩牌人次"
        },
        {
          prop:"game_num",
          label:"牌局数"
        },
        {
          prop:"win_num",
          label:"赢局数"
        },
        {
          prop:"lost_num",
          label:"输局数"
        },
        {
          prop:"win",
          label:"系统总赢金"
        },
        {
          prop:"lost",
          label:"系统总输金"
        },
        {
          prop:"consume",
          label:"总消耗"
        },
        {
          prop:"bankrupt_user",
          label:"破产用户数"
        },
        {
          prop:"bankrupt_ratio",
          label:"破产率"
        },
      ]
    };
  },
  created() {
    let today = new Date().getTime();
    this.start_time = this.initTime(today);
    this.end_time = this.initTime(today - 60 * 60 * 24 * 7 * 1000);
    // console.log(this.start_time, this.end_time);
    this.initChannel();
    this.getData();
  },
  methods: {
    search() {
        this.getData()
    },

    //页容量变化
    handleSizeChange(num) {
        this.limit = num
        this.currentPage = 1
        this.getData()
    },

    //页码变化
    handleCurrentChange(pagenum) {
        this.currentPage = pagenum
        this.getData()
    },

    getData() {
      this.startDate = this.start_time;
      this.startDate = DeepData(this.startDate).replace(/[-]/g, "");
      this.endDate = this.end_time;
      this.endDate = DeepData(this.endDate).replace(/[-]/g, "");

      this.initData({
        page: this.currentPage,
        limit: this.limit,
        start_date: +this.startDate,
        end_data: +this.endDate,
        channel_name: this.avator_nameO === "所有渠道" ? "" : this.avator_nameO
      });
    },

    //获取时间格式
    initTime(today) {
      let myDate = new Date(today);
      let year = myDate.getFullYear();
      let month = myDate.getMonth() + 1;
      let day = myDate.getDate();
      return `${year}-${month}-${day}`;
    },

    //获取所有渠道
    async initChannel() {
      let Channelsdata = await this.$http.OperationMan.GetChannels();
      let changeData = [];
      Channelsdata.data.data.forEach(item => {
        changeData.push(item.name);
      });
      changeData.forEach((item, index) => {
        this.optionchannels.push({ avator_nameO: item, levelO: index });
      });
      this.optionchannels.unshift({ avator_nameO: "所有渠道", levelO: -1 });
    },

    //获取表格数据
    async initData(params) {
      let { data } = await this.$http.DataAnalysis.GetrMatchOverview(params);
      this.tableData = data.data;
      this.total = data.total;
    }
  }
};



